export default {
  computed: {
    noun() {
      let n = this.totalProducts;
      n %= 100;
      if (n >= 5 && n <= 20) {
        return 'товаров';
      }
      n %= 10;
      if (n === 1) {
        return 'товар';
      }
      if (n >= 2 && n <= 4) {
        return 'товара';
      }
      return 'товаров';
    },
  },
};
